.item{
    display: inline-flex;
}
.item a{
    text-decoration: none;
    text-transform: uppercase;
    color: #000;
    font-weight: 600;

}
.nav_menu{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.item a.activeAnkor{
    color: rgb(0, 68, 253);
}
.content {
    grid-area: c;
    color: rgb(61, 61, 61);
    padding: 25px;
    margin: 15px;
    background-color: #fff;
    box-shadow: 0px 1px 15px 0px rgb(51 51 51 / 20%);
  }
  @media (max-width: 980px) {
    .content {
        grid-area: d;
        color: rgb(61, 61, 61);
        padding: 25px;
        margin: 15px;
        background-color: #fff;
        box-shadow: 0px 1px 15px 0px rgb(51 51 51 / 20%);
        display: none;
      }
  }