.nav {
  grid-area: n;
  color: rgb(61, 61, 61);
  padding: 25px;
  margin: 15px;
  background-color: #fff;
  box-shadow: 0px 1px 15px 0px rgb(51 51 51 / 20%);
}
.item a{
  color: #000;
  text-decoration: none;
}
.item a.activeAnkor{
  color: gold;
}
.nav img{
  width: 100%;
}
.text{
  font-weight: bold;
  padding-top: 1rem;
}
