 .app-wrapper {
   display: grid;
   min-width: 980px;
   max-width: 1200px;
   margin: 0 auto;
   grid-template-rows: 110px 1fr;
   grid-template-columns: 3fr 9fr;
   grid-template-areas: 
   "h h"
   "n c";
 }

body{
  background-color: #f1f1f1;
}

@media (max-width: 980px) {
  .app-wrapper {
    display: grid;
    min-width: 200px;
    max-width: 100%;
    margin: 0 auto;
    grid-template-rows: 150px 1fr;
    grid-template-columns: 12fr;
    grid-template-areas:
        "h"
        "n"
        "c"
        "d";
  }
}